.ui-autocomplete {
				position: absolute;
				list-style: none;
				background-color: #fff;
				top: 100% !important;
				left: -1px !important;
				right: -1px !important;
				width: auto !important;
				border: 1px solid #d4dde6;
				line-height: 1.14286;
				margin: 0;
				padding: 0;
				box-shadow: 0 4px 4px 0 rgba(212, 221, 230, 0.61803);
}

.ui-autocomplete:empty {
				display: none !important;
}

.ui-autocomplete:focus {
				outline: none;
				box-shadow: inset 0 0 0 3px #7a9a01;
}

.ui-menu-item {
				color: #afbac3;
				letter-spacing: .28px;
}

.ui-menu-item:focus {
				outline: none;
				box-shadow: inset 0 0 0 3px #7a9a01;
}

.ui-menu-item-wrapper {
				position: relative;
				display: block;
				padding: 8px 16px 8px 71px;
}

.ui-menu-item-wrapper:hover {
				text-decoration: none;
}

.ui-menu-item-wrapper:hover, .ui-menu-item-wrapper:active, .ui-menu-item-wrapper.ui-state-active {
				color: #fff;
				background-color: #7a9a01;
}

.search {
				background-color: #fff;
}

.search__align {
				display: flex;
				justify-content: space-between;
				align-items: center;
}

.search__item {
				flex-grow: 1;
				height: 56px;
}

.search__input {
				border: none;
				letter-spacing: .28px;
				display: block;
				width: 100%;
				height: 100%;
				padding: 8px 16px;
				padding: .5rem 1rem;
}

.search__input::-moz-placeholde {
				color: #afbac3;
				opacity: 1;
				font-weight: 300;
}

.search__input:-ms-input-placeholder {
				color: #afbac3;
				font-weight: 300;
}

.search__input::-webkit-input-placeholder {
				color: #afbac3;
				font-weight: 300;
}

.search__input:focus {
				outline: none;
}

@media (max-width: 480px) {
				.search__input {
								height: 90%;
								margin: 1.1% 0;
								border-radius: 2px;
								margin-left: 4px;
				}
}

.search__submit, .search__clear {
				background: none;
				border: none;
				display: block;
				line-height: 1.14286;
				height: 100%;
				width: 100%;
				padding: 0;
				color: #afbac3;
}

.search__submit[class*='custom-font']::before, .search__clear[class*='custom-font']::before {
				display: block;
				margin: 0 auto;
				font-size: 24px;
				font-size: 1.5rem;
				min-width: 24px;
				min-width: 1.5rem;
}

.search__submit:focus, .search__clear:focus {
				outline: none;
				box-shadow: inset 0 0 0 3px #d4dde6;
}

.search__submit:hover, .search__clear:hover {
				color: #fff;
				background-color: #d4dde6;
}

.search__submit:active, .search__clear:active {
				color: #fff;
				background-color: #c4d1dd;
				box-shadow: none;
}

.search__item--submit {
				flex-basis: 56px;
				flex-shrink: 0;
}

.search__item--input {
				width: 100%;
}

.search__item--clear {
				flex-basis: 75px;
				flex-shrink: 0;
}
